le al nivel de la tropósfera”. (Decreto 948 de 1995)")])])],1)])]),_vm._m(63)]),_c('div',{staticClass:"row mt-4"},[_vm._m(64),_c('div',{staticClass:"col-lg-6"},[_c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-md-12 mb-5 mb-md-0"},[_c('LineaTiempoD',{staticClass:"color-secundario"},[_c('p',{attrs:{"numero":"6","titulo":"Umbral de molestia:"}},[_vm._v(" Es la concentración a la que una pequeña parte de la población (< 5%) manifiesta molestias durante un periodo corto de tiempo. Dado que la sensación de molestia puede estar influenciada por factores sicológicos y socioeconómicos un umbral de molestia no puede definirse solo con base en la concentración.")]),_c('p',{attrs:{"numero":"7","titulo":"Umbral de tolerancia"}},[_vm._v(" “es el olor generado por sustancias o actividades industriales, comerciales o de servicio, que produce fastidio, aunque no cause daño a la salud humana”. (Decreto 948 de1995)")]),_c('p',{attrs:{"numero":"8","titulo":"Unidad de olor:"}},[_vm._v(" “es la cantidad de (una mezcla de) sustancias olorosas presentes en un metro cúbico de gas oloroso (en condiciones normales 1 atmósfera de presión y 0 °C de temperatura) origina una respuesta fisiológica en el umbral del panel”. (Resolución 1541 de 2013)")]),_c('p',{attrs:{"numero":"9","titulo":"Unidad de olor Europea:"}},[_vm._v(" “cantidad de sustancia olorosa qué coma cuando se evapora en 1 metro cúbico de gas neutro en condiciones normales, origina una respuesta fisiológica de un panel (umbral de detección) equivalente al que origina una masa de olor de referencia europea more evaporada en 1 metro cúbico de gas neutro en condiciones normales”. (Resolución 1541 de 2013)")]),_c('p',{attrs:{"numero":"10","titulo":"Olfatometría:"}},[_vm._v(" técnica sensorial de medición de olores que se usa para determinar el grado de molestia que pueden ocasionar ciertos olores a la población.")])])],1)])])])])]),_vm._m(65),_c('separador'),_vm._m(66),_c('p',[_vm._v("Pruebe colocarse los audífonos para esc