nto de Vigilancia Amb., No. 485/10, 2012 p. 24)")])]),_c('div',{staticClass:"col-md-3"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema8/img45.png"),"alt":"Texto que describa la imagen"}})])])])])]),_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-lg-11"},[_c('div',{staticClass:"row align-items-center"},[_c('div',{staticClass:"col-md-9 mb-4 mb-md-0"},[_c('h5',[_vm._v("Rellenos Sanitarios. ")]),_c('p',[_vm._v("Desde la aparición de las ciudades han existido problemas de salud pública por la generación de residuos, Los olores ofensivos provenientes de los rellenos sanitarios generalmente ocurren por un mal funcionamiento de los biofiltros o prácticas inadecuadas, como reutilización de áreas previamente utilizadas o por descargas incontroladas de residuos. Esta percepción de los malos olores no es permanente, sino que su presencia está asociada a las condiciones del clima y a la dirección de los vientos, siendo las horas nocturnas y los días cálidos que siguen a las lluvias, cuando los olores son más intensos. (Lineamiento de Vigilancia Amb., No. 485/10, 2012 p.28)")])]),_c('div',{staticClass:"col-md-3"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema8/img46.png"),"alt":"Texto que describa la imagen"}})])])])])]),_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-lg-11"},[_c('div',{staticClass:"row align-items-center"},[_c('div',{staticClass:"col-md-9 mb-4 mb-md-0"},[_c('h5',[_vm._v("Curtido de Cuero.")]),_c('p',[_vm._v("las industrias de curtido y acabado de cueros, tradicionalmente han aparecido ante el mundo como algunas de las más sucias y contaminantes denido al proceso para que este producto tenga el terminado que conocemos comercialmente, como consecuencia El agua residual de la industria de curtido tiene altas concentraciones de materia orgánica,compuestos de nitrógeno, sulfuros, sólidos suspendidos, compuestos de cromo y pH elevado.")]),_c('p',[_vm._v("La alta carga de materia orgánica provoca la creación de condiciones anaerobias en cuerpos o cursos de agua, debido al elevado consumo de oxígeno disuelto. Estas condiciones, además de afectar la vida acuática, favorecen la producción de algunos gases nocivos como ácido sulfhídrico y metano indicadores cualitativos para la generacion de olores ofensivos de muy mal carácter. (Lineamiento de Vigilancia Amb., No. 485/10, 2012 p. 30) ")]),_c('p',{staticClass:"mb-0"},[_vm._v("Generalmente los malos olores provienen de un inadecuado control en las diferentes actividades , las mas significativas son:")]),_c('p',[_vm._v("El pelambre, descarne, la limpieza de equipos y recipientes y el vertimiento de efluentes líquidos y sólidos a los cursos de agua.")])]),_c('div',{staticClass:"col-md-3"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema8/img47.png"),"alt":"Texto que describa la imagen"}})])])])])]),_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-lg-11"},[_c('div',{staticClass:"row align-items-center"},[_c('div',{staticClass:"col-md-9 mb-4 mb-md-0"},[_c('h5',[_vm._v("Industria petroquímica.")]),_c('p',[_vm._v("“En la industria petrolera, los yacimientos de crudo contienen contaminantes entre los cuales se encuentra el azufre, el cual al combinarse con moléculas de hidrogeno forma el ácido sulfhídrico, que es liberado de los hidrocarburos en forma de gas al suministrarles calor. Existen microorganismos anaerobios capaces de utilizar compuestos de azufre, tales como las bacterias sulfato reductoras (BSR) y las bacterias tiosulfato reductoras (BSTR), las cuales utilizan el azufre como aceptores finales de electrones en la oxidación de la materia orgánica, produciendo principalmente ácido sulfhídrico y mercaptanos”. (Lineamiento de Vigilancia Amb., No. 485/10, 2012 p.30).")]),_c('p',[_vm._v("Otro aspecto importante que tiene que ver con la industria petrolera y la contaminación por olores ofensivos son los derrames de petróleo debido a la ocurrencia de accidentes o atentados a la infraestrucctra petrolera en nuestro pais influyendo en la salud de las poblaciones afectadas.")])]),_c('div',{staticClass:"col-md-3"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema8/img48.png"),"alt":"Texto que describa la imagen"}})])])])])])])],1),_vm._m(74),_c('separador'),_vm._m(75),_c('p',[_vm._v("Un olor que en principio es agradable cuando es percibido con tanta intensidad puede convertirse en molesto, es por eso que el concepto de olor ofensivo también se vuelve muy subjetivo, debido a que los olores dependen específicamente de la intensidad o fuerza y de la concentración en aire del compuesto(s) que lo origina.")]),_c('p',[_vm._v("Así como el ruido, las vibraciones y la luz, el olor es considerado un estresor ambiental. Según la Organización Mundial de la Salud (OMS) “La salud es un estado de completo bienestar físico, mental y social, y no solamente la ausencia de afecciones o enfermedades”. Así la molestia por olor afecta la calidad de vida de las personas. (Fuente: https://olores.mma.gob.cl/efectos-en-salud/)")]),_c('p',[_vm._v("Algunos estudios señalan que los olores pueden afectar al estado psíquico de las personas, influyendo negativamente sobre su estado anímico y pudiendo provocar situaciones de estrés, por esta razón, el olor es considerado un contaminante. Fuente: https://olores.mma.gob.cl/efectos-en-salud/)")]),_c('p',[_vm._v("En resumen, algunos síntomas negativos provocados por la molestia de olor son:")]),_vm._m(76),_c('p',{staticClass:"mt-3"},[_vm._v("Algunos de estos efectos dependen de la dosis y pueden aumentar con el tiempo.")]),_vm._m(77),_c('separador'),_vm._m(78),_c('p',[_vm._v("Para la medición de olores ofensivos la resolución 627 del año 2006 establece como referencia el protocolo para la medición de olores ofensivos en el cual establecen los procedimientos y caracterización de olores, en esta sección veremos las condiciones generales de la medición en campo en olores ofensiv